              <el-table-column prop="phone" label="ç”µè¯" width="150"></el-table-column>
              <el-table-column prop="email" label="é‚®ç®±" width="200"></el-table-column>
              <el-table-column prop="company" label="å…¬å¸" width="120"></el-table-column>
              <el-table-column prop="address" label="åœ°å€" show-overflow-tooltip></el-table-column>
            </el-table>
            <p v-if="contacts.length > 5" class="preview-tip">
              ä»…æ˜¾ç¤ºå‰5æ¡æ•°æ®ï¼Œå®Œæ•´æ•°æ®å°†åœ¨Excelä¸­æ˜¾ç¤º
            </p>
          </div>

          <!-- è½¬æ¢é€‰é¡¹ -->
          <div v-if="contacts.length > 0" class="options-section">
            <h3>å¯¼å‡ºé€‰é¡¹</h3>
            <el-row :gutter="20">
              <el-col :span="12">
                <el-checkbox v-model="exportOptions.includeEmpty">åŒ…å«ç©ºå­—æ®µåˆ—</el-checkbox>
              </el-col>
              <el-col :span="12">
                <el-checkbox v-model="exportOptions.formatPhone">æ ¼å¼åŒ–ç”µè¯å·ç </el-checkbox>
              </el-col>
            </el-row>
          </div>

          <!-- æ“ä½œæŒ‰é’® -->
          <div class="action-section">
            <el-button 
              type="primary" 
              @click="convertToExcel"
              :disabled="contacts.length === 0"
              :loading="converting"
              size="large">
              <i class="el-icon-download"></i>
              è½¬æ¢å¹¶ä¸‹è½½Excelæ–‡ä»¶
            </el-button>
            
            <el-button @click="clearAll" size="large">
              <i class="el-icon-refresh"></i>
              æ¸…ç©ºé‡ç½®
            </el-button>
          </div>

          <!-- ä½¿ç”¨è¯´æ˜ -->
          <div class="usage-guide">
            <h3>ä½¿ç”¨è¯´æ˜</h3>
            <div class="guide-content">
              <div class="guide-section">
                <h4>æ”¯æŒæ ¼å¼</h4>
                <ul>
                  <li>è¾“å…¥æ ¼å¼ï¼šVCF (vCard) é€šè®¯å½•æ–‡ä»¶</li>
                  <li>è¾“å‡ºæ ¼å¼ï¼šExcelæ–‡ä»¶ (.xlsx)</li>
