                  <li>ç‚¹å‡»è½¬æ¢æŒ‰é’®ä¸‹è½½Excelæ–‡ä»¶</li>
                  <li>ä½¿ç”¨Excelç¼–è¾‘å’Œç®¡ç†è”ç³»äººä¿¡æ¯</li>
                </ol>
              </div>

              <div class="guide-section">
                <h4>å¯¼å‡ºå­—æ®µ</h4>
                <ul>
                  <li><strong>å§“å</strong>ï¼šè”ç³»äººå§“å</li>
                  <li><strong>ç”µè¯</strong>ï¼šä¸»è¦ç”µè¯å·ç </li>
                  <li><strong>é‚®ç®±</strong>ï¼šç”µå­é‚®ä»¶åœ°å€</li>
                  <li><strong>å…¬å¸</strong>ï¼šå·¥ä½œå•ä½</li>
                  <li><strong>åœ°å€</strong>ï¼šè”ç³»åœ°å€</li>
                  <li><strong>å¤‡æ³¨</strong>ï¼šå…¶ä»–ä¿¡æ¯</li>
                </ul>
              </div>

              <div class="guide-section">
                <h4>æ³¨æ„äº‹é¡¹</h4>
                <ul>
                  <li>æ”¯æŒä»iPhoneã€Androidã€Outlookç­‰è®¾å¤‡å¯¼å‡ºçš„VCFæ–‡ä»¶</li>
                  <li>è‡ªåŠ¨è¯†åˆ«ä¸­æ–‡å’Œè‹±æ–‡è”ç³»äººä¿¡æ¯</li>
                  <li>å¤šä¸ªç”µè¯å·ç ä¼šåˆå¹¶æ˜¾ç¤ºï¼Œç”¨åˆ†å·åˆ†éš”</li>
                  <li>ç©ºå­—æ®µå¯é€‰æ‹©æ˜¯å¦åœ¨Excelä¸­æ˜¾ç¤º</li>
                  <li>ç”Ÿæˆçš„Excelæ–‡ä»¶å¯ç›´æ¥ç”¨äºExcelè½¬VCFåŠŸèƒ½</li>
                </ul>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
