<script>
import * as XLSX from 'xlsx'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'VcfToExcelView',
  data() {
    return {
      metaInfo: {
        title: 'VCFè½¬Excelé€šè®¯å½• - è”ç³»äººç®¡ç†å·¥å…·',
        meta: [
          { name: 'description', content: 'å°†VCFé€šè®¯å½•æ–‡ä»¶è½¬æ¢ä¸ºExcelè¡¨æ ¼ï¼Œæ–¹ä¾¿ç¼–è¾‘å’Œç®¡ç†è”ç³»äººä¿¡æ¯ã€‚' },
          { name: 'keywords', content: 'VCFè½¬Excel,é€šè®¯å½•è½¬æ¢,è”ç³»äººå¯¼å‡º,VCFè§£æ,Excelç”Ÿæˆ' }
        ]
      },
      fileList: [],
      contacts: [],
      converting: false,
      exportOptions: {
        includeEmpty: false,
        formatPhone: true
      }
    }
  },
  computed: {
    previewContacts() {
      return this.contacts.slice(0, 5)
    }
  },
  methods: {
    beforeUpload(file) {
      const isVcf = file.type === 'text/vcard' || file.name.toLowerCase().endsWith('.vcf')
      const isLt10M = file.size / 1024 / 1024 < 10

      if (!isVcf) {
        this.$message.error('åªèƒ½ä¸Šä¼ VCFæ ¼å¼çš„æ–‡ä»¶!')
        return false
      }
      if (!isLt10M) {
        this.$message.error('æ–‡ä»¶å¤§å°ä¸èƒ½è¶…è¿‡10MB!')
        return false
      }
      return false // é˜»æ­¢è‡ªåŠ¨ä¸Šä¼ 
    },

    async handleFileChange(file) {
      if (file.raw) {
        this.fileList = [file]
        await this.parseVcfFile(file.raw)
      }
    },

    async parseVcfFile(file) {
      try {
        const content = await this.fileToText(file)
        this.contacts = this.parseVcfContent(content)
        
        if (this.contacts.length === 0) {
          this.$message.warning('æœªæ‰¾åˆ°æœ‰æ•ˆçš„è”ç³»äººä¿¡æ¯')
        } else {
          this.$message.success(`æˆåŠŸè§£æ ${this.contacts.length} ä¸ªè”ç³»äºº`)
        }
      } catch (error) {
        console.error('è§£æVCFæ–‡ä»¶å¤±è´¥:', error)
        this.$message.error('è§£æVCFæ–‡ä»¶å¤±è´¥: ' + error.message)
      }
    },

    fileToText(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.onload = e => resolve(e.target.result)
        reader.onerror = reject
        reader.readAsText(file, 'UTF-8')
      })
    },

    parseVcfContent(content) {
      const contacts = []
      const vcardBlocks = content.split(/BEGIN:VCARD/i).slice(1)

      vcardBlocks.forEach(block => {
        try {
          const contact = this.parseVcardBlock('BEGIN:VCARD' + block)
          if (contact.name || contact.phone) {
            contacts.push(contact)
          }
        } catch (error) {
          console.warn('è§£ævCardå—å¤±è´¥:', error)
        }
      })

      return contacts
    },

    parseVcardBlock(vcardText) {
      const contact = {
        name: '',
        phone: '',
        email: '',
        company: '',
        address: '',
        note: ''
      }

      const lines = vcardText.split(/\r?\n/)
      
      lines.forEach(line => {
        line = line.trim()
        if (!line) return

        // è§£æå§“å
        if (line.match(/^FN[:;]/i)) {
          contact.name = this.extractVcfValue(line)
        }
        
        // è§£æç”µè¯
        if (line.match(/^TEL[:;]/i)) {
          const phone = this.extractVcfValue(line)
          if (phone) {
            contact.phone = contact.phone ? contact.phone + '; ' + phone : phone
          }
        }
        
        // è§£æé‚®ç®±
        if (line.match(/^EMAIL[:;]/i)) {
          const email = this.extractVcfValue(line)
          if (email) {
            contact.email = contact.email ? contact.email + '; ' + email : email
          }
        }
        
        // è§£æå…¬å¸
        if (line.match(/^ORG[:;]/i)) {
          contact.company = this.extractVcfValue(line)
        }
        
        // è§£æåœ°å€
        if (line.match(/^ADR[:;]/i)) {
          const address = this.extractVcfValue(line)
          // å¤„ç†åœ°å€æ ¼å¼ï¼Œç§»é™¤å¤šä½™çš„åˆ†å·
          contact.address = address.replace(/;+/g, ' ').trim()
        }
        
        // è§£æå¤‡æ³¨
        if (line.match(/^NOTE[:;]/i)) {
          contact.note = this.extractVcfValue(line)
        }
      })

      // æ ¼å¼åŒ–ç”µè¯å·ç 
      if (this.exportOptions.formatPhone && contact.phone) {
        contact.phone = this.formatPhoneNumbers(contact.phone)
      }

      return contact
    },

    extractVcfValue(line) {
      const colonIndex = line.indexOf(':')
      if (colonIndex === -1) return ''
      
      let value = line.substring(colonIndex + 1).trim()
      
      // åè½¬ä¹‰VCFç‰¹æ®Šå­—ç¬¦
      value = value
        .replace(/\\n/g, '\n')
        .replace(/\\;/g, ';')
        .replace(/\\,/g, ',')
        .replace(/\\\\/g, '\\')
      
      return value
    },

    formatPhoneNumbers(phoneString) {
      return phoneString.split(';').map(phone => {
        phone = phone.trim()
        // ç§»é™¤æ ¼å¼å­—ç¬¦
        phone = phone.replace(/[\s\-()ï¼ˆï¼‰]/g, '')
        // æ ¼å¼åŒ–ä¸­å›½æ‰‹æœºå·
        if (/^(\+86)?1[3-9]\d{9}$/.test(phone)) {
          phone = phone.replace(/^\+?86/, '')
          return phone.replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3')
        }
        return phone
      }).join('; ')
    },

    async convertToExcel() {
      if (this.contacts.length === 0) {
        this.$message.warning('æ²¡æœ‰å¯è½¬æ¢çš„è”ç³»äººæ•°æ®')
        return
      }

      this.converting = true

      try {
        // å‡†å¤‡Excelæ•°æ®
        const headers = ['å§“å', 'ç”µè¯', 'é‚®ç®±', 'å…¬å¸', 'åœ°å€', 'å¤‡æ³¨']
        const data = [headers]

        this.contacts.forEach(contact => {
          const row = [
            contact.name || '',
            contact.phone || '',
            contact.email || '',
            contact.company || '',
            contact.address || '',
            contact.note || ''
          ]
          
          // å¦‚æœä¸åŒ…å«ç©ºå­—æ®µï¼Œæ£€æŸ¥æ˜¯å¦æœ‰æœ‰æ•ˆæ•°æ®
          if (!this.exportOptions.includeEmpty) {
            if (row.slice(1).some(cell => cell.trim())) {
              data.push(row)
            }
          } else {
            data.push(row)
          }
        })

        // åˆ›å»ºExcelæ–‡ä»¶
        const wb = XLSX.utils.book_new()
        const ws = XLSX.utils.aoa_to_sheet(data)

        // è®¾ç½®åˆ—å®½
        const colWidths = [
          { wch: 12 }, // å§“å
          { wch: 18 }, // ç”µè¯
          { wch: 25 }, // é‚®ç®±
          { wch: 15 }, // å…¬å¸
          { wch: 25 }, // åœ°å€
          { wch: 20 }  // å¤‡æ³¨
        ]
        ws['!cols'] = colWidths

        // è®¾ç½®æ ‡é¢˜è¡Œæ ·å¼
        const range = XLSX.utils.decode_range(ws['!ref'])
        for (let col = range.s.c; col <= range.e.c; col++) {
          const cellAddress = XLSX.utils.encode_cell({ r: 0, c: col })
          if (ws[cellAddress]) {
            ws[cellAddress].s = {
              font: { bold: true },
              fill: { fgColor: { rgb: "FFCCCCCC" } }
            }
          }
        }

        XLSX.utils.book_append_sheet(wb, ws, 'é€šè®¯å½•')

        // ç”Ÿæˆæ–‡ä»¶åå¹¶ä¸‹è½½
        const filename = DownloadUtil.generateTimestampFilename('é€šè®¯å½•', 'xlsx')
        XLSX.writeFile(wb, filename)

        this.$message.success(`Excelæ–‡ä»¶å·²ç”Ÿæˆï¼å…±å¯¼å‡º ${data.length - 1} ä¸ªè”ç³»äºº`)
      } catch (error) {
        console.error('è½¬æ¢å¤±è´¥:', error)
        this.$message.error('è½¬æ¢å¤±è´¥: ' + error.message)
      } finally {
        this.converting = false
      }
    },

    clearAll() {
      this.fileList = []
      this.contacts = []
      this.exportOptions = {
        includeEmpty: false,
        formatPhone: true
      }
      this.$message.info('å·²æ¸…ç©ºæ‰€æœ‰æ•°æ®')
    }
  }
}
</script>

